import { getArticleDetails } from '@/api/articles'
import MarkdownIt from 'markdown-it'
import ArticleMete from "./components/article-meta";
import ArticleComment from "./components/article-comment";

export default {
  name: 'ArticleIndex',
  async asyncData ({ params }) {
    const { data } = await getArticleDetails(params.slug)
    const { article } = data
    const md = new MarkdownIt()
    md.render(article.body)

    return {
      article: article
    }
  },
  components: {
    ArticleMete,
    ArticleComment
  },

  head () {
    return {
      title: `${this.article.title} - ReallWorld`,
      meta: [
        {
          hid: 'description',
          name: 'description',
          content: this.article.description
        }
      ]
    }
  }

}
