import { delArticle } from "@/api/articles";
import { mapState } from 'vuex'
export default {
  name: 'ArticleMete',
  computed: {
    ...mapState(['user'])
  },
  props: {
    article: {
      type: Object,
      required: true
    }
  },
  methods: {
    async del () {
      try {
        const { data } = await delArticle(this.article.slug)
        this.$router.go(-1)
      } catch (error) {
        console.log(error)
      }
    }
  }

}
